<template>
  <div id='app'>
  <v-app id="list-sample">
    <v-flex sm6 offset-sm3>
      <input type="checkbox" id="repeat" value="" v-model="checkedTag">
      <label for="repeat"></label>
      &nbsp;
      <input type="checkbox" id="korean" value="" v-model="checkedTag">
      <label for="korean"></label>
      &nbsp;
      <input type="checkbox" id="chinese" value="" v-model="checkedTag">
      <label for="chinese"></label>
      &nbsp;
      <input type="checkbox" id="japanese" value="" v-model="checkedTag">
      <label for="japanese"></label>
      &nbsp;
      <input type="checkbox" id="verse" value="" v-model="checkedTag">
      <label for="verse"></label>
      &nbsp;
      <input type="checkbox" id="prose" value="" v-model="checkedTag">
      <label for="prose"></label>
      &nbsp;
      <input type="checkbox" id="complicated" value="" v-model="checkedTag">
      <label for="complicated"></label>
      &nbsp;
      <input type="checkbox" id="unformal" value="" v-model="checkedTag">
      <label for="unformal"></label>
      &nbsp;
      <input type="checkbox" id="pro-jap" value="" v-model="checkedTag">
      <label for="pro-jap"></label>
      <v-text-field label="Search by Title"
                    v-model="search">
      </v-text-field>
      <v-list two-line
              v-for="(listItem, index) in calData" :key="index">
        <v-list-tile>
          <v-list-tile-content>
            <v-list-tile-title class="text--primary" >
              <router-link :to="{path:'/poems/'+listItem.id}">{{ listItem.title }} &nbsp &nbsp &nbsp tags: {{listItem.tags}}</router-link>
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-divider></v-divider>
      </v-list>
      <br/>
      <v-pagination
        v-model="curPageNum"
        :length="numOfPages">
      </v-pagination>
    </v-flex>

  </v-app>
  </div>
</template>



<script>
  import testData from '@/assets/testData.js'
  export default {
    data() {
      return {
        checkedTag: [],
        search: '',
        listData: [],
        searchData: [],
        dataPerPage: 10,
        curPageNum: 1,
        curSelectIndex: 0,
      }
    },
    created() {
      this.listData=testData
    },
    computed: {
      startOffset() {
        return ((this.curPageNum - 1) * this.dataPerPage);
      },
      endOffset() {
        return (this.startOffset + this.dataPerPage);
      },
      numOfPages() {
        return Math.ceil(this.searchData.length / this.dataPerPage);
      },
      calData() {
        this.searchData = this.listData.filter((data) => {
          return data.title.toLowerCase().includes(this.search.toLowerCase())
        }).slice(0);

        for (var value of this.checkedTag) {
          this.searchData = this.searchData.filter((data) => {
          return data.tags.includes(value)
        })
        }

        return this.searchData.slice(this.startOffset, this.endOffset)
      }
    }
  }
</script>
